@import url(https://fonts.googleapis.com/css2?family=Bowlby+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}:root{--font-logo: 'Bowlby One', cursive;--font-text: 'Roboto', sans-serif}:root{--black: #212121;--white: #fdfdfd;--grey: #393939;--logo_blue: #00CFFB;--logo_green: #9AFB00}:root{font-size:16px;font-family:'Roboto', sans-serif;font-family:var(--font-text)}@media screen and (min-width: 320px){:root{font-size:calc(16px + 6 * ((100vw - 320px) / 680))}}@media screen and (min-width: 1000px){:root{font-size:22px}}

.app{display:flex;flex-direction:column}

footer{display:flex;justify-content:space-between;align-items:center;margin:0;padding:1px 10px;height:40px;background:var(--black);color:var(--white);font-size:.6rem}footer .social{font-size:30px}footer .social a{color:var(--white)}

@-webkit-keyframes menuSlide{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes menuSlide{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}header{display:flex;align-items:flex-start;justify-content:space-between;margin:0 15px;height:12vw}header h1{text-transform:uppercase;font-size:10vw;transition:.25s ease-in-out;max-width:1200px}header h1 a{color:var(--logo_green);font-family:var(--font-logo);text-decoration:none}header h1 a span{color:var(--logo_blue)}.hamburger_icon{color:var(--black);font-size:10vw;-webkit-transform:translateY(0);transform:translateY(0);transition:.35s ease-in-out}.hamburger_links{display:none;padding-top:20vh;position:absolute;top:0;left:0;width:100vw;height:80vh;background:var(--white);box-shadow:-8px 0px 40px 0px rgba(0,0,0,0.2);text-align:center;-webkit-transform:translateX(100%);transform:translateX(100%);animation:menuSlide .5s ease-in-out reverse;z-index:99}.hamburger_links #where{margin-bottom:10vh;color:var(--grey);font-size:.9em}.hamburger_links .times_icon{position:absolute;top:5vh;right:5vh;font-size:2em}.hamburger_links li{margin-top:7vh}.hamburger_links li .link{color:var(--black);text-transform:uppercase;text-decoration:none;font-weight:bold}.hamburger_links-visible{display:block;-webkit-transform:translate(0);transform:translate(0);-webkit-animation:menuSlide .5s ease-in-out;animation:menuSlide .5s ease-in-out}@media (min-width: 768px){header{width:98vw;margin:0 auto;height:72px}.hamburger_icon{display:none}.hamburger_links{display:flex;width:auto;height:auto;padding:0;margin:0;position:static;background:rgba(0,0,0,0);box-shadow:none;-webkit-transform:translateX(0%);transform:translateX(0%);transition:none;z-index:0}.hamburger_links .times_icon,.hamburger_links #where{display:none}.hamburger_links li{display:flex;margin-top:0;padding:25px}.hamburger_links li a:hover{-webkit-text-decoration:underline solid var(--logo_green) 4px;text-decoration:underline solid var(--logo_green) 4px}.hamburger_links li:first-of-type{display:none}.logo h1{font-size:4vw}}


.wrapper{display:flex;margin:0 auto;flex-direction:column;min-height:calc(100vh - 12vh - 5vh);max-width:1200px}

.news{display:flex;flex-direction:column;width:90vw;margin:15px}.news .news_data-title{font-size:1.5em;font-weight:bold;text-transform:uppercase}.news .news_data-date{font-size:.7em}.news .news_img{width:100%;margin:10px 0}.news .news_data-content{line-height:1.4}@media (min-width: 768px){.news{width:800px;margin:50px auto}}

.posts{max-width:100vw}

.offer{margin:0 auto;min-height:calc(100vh - 14px - 100px);max-width:1200px}

.ski_service{min-height:calc(100vh - 12vh - 40px);margin:25px 15px;line-height:1.4;max-width:1200px}.ski_service p{margin-bottom:20px}.ski_service p li{margin:10px 30px;font-weight:bold;list-style-type:disc}@media (min-width: 768px){.ski_service{min-height:calc(100vh - 14px - 100px);margin:10vh auto 0;padding:0 25px}}


.googlemap{height:40vh;width:100%}.map_marker{font-size:3em;color:var(--logo_blue)}.timetable{display:flex;flex-direction:column;justify-content:center;max-width:1200px;height:auto;min-height:calc(100vh - 40px - 40vh - 12vw);margin:0 auto;text-align:center}.timetable p{line-height:1.4}.timetable p strong{font-weight:bold}.timetable p span{display:inline-block;text-transform:uppercase;font-weight:bold;margin:20px 0 7px}@media (min-width: 768px){.googlemap{height:60vh}.timetable{flex-direction:row;align-items:center}.timetable p{margin:0 150px}}


